.services{
grid-column: 1 / -1;
grid-row:5;
display:grid;
grid-template-columns:1fr;
grid-template-rows: 70px auto 100px;
background:$color-primary;
width:100%;
height:100%;

& > .s_header{
grid-column: 1 / -1;
grid-row:1;
text-align:center;
display:flex;
align-items:center;
justify-content:center;
@include text-template($small,$font-family-ads, $color-primary);
background:$color-dark-headers;

& > h2{
color:$color-primary;
}
}//header

& > .s-row{
grid-column: 1 / -1;
grid-row:2;
display:grid;
grid-template-columns: 1f;
grid-template-rows: auto;
grid-gap:30px;
padding:30px;

@include responsive(phone){
grid-template-columns: 1fr 1fr;
}


@include responsive(big-tablet){
grid-template-columns: 1fr 1fr 1fr;
min-height:600px;
}

@include responsive(desktop){
grid-template-columns: 1fr 1fr 1fr 1fr;
min-height:600px;
}

@include responsive(developer){
grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
min-height:300px;
}

& > section{
background:$color-primary;
height:300px;
border-radius: $header-b-rad;
padding: $pad-med;
text-align:center;
@include text-template($small,$font-family-ads, $color-primary);
width:100%;
justify-self:center;
align-self:center;

@include responsive(phone){
height:350px;
}//end laptop

@include responsive(tablet){
height:300px;
}//end laptop


@include responsive(desktop){
height:350px;
}//end laptop

@include responsive(developer){
height:450px;
}//end laptop

@include responsive(developer){
height:400px;
}//end laptop

& > .icon-service{
font-size:40px;
color:$color-services-icons;
margin:20px 0px 0px 0px;
}

& > h2{
color:$color-dark;
margin:20px 0px 20px 0px ;

@include responsive(laptop){
font-size:$med;}//end laptop
}

& > p{
@include text-r($font-family-base, $less-big, 300, $color-services-text);
line-height:26px;


@include responsive(galaxy-fold){
font-size:$tiny;
}//end laptop


@include responsive(phone){
font-size:$normal;
}//end laptop

@include responsive(tablet){
font-size:$small;
}//end laptop

@include responsive(laptop){
font-size:$normal;
}//end laptop

@include responsive(desktop){
font-size:$normal;
}//end laptop

@include responsive(developer){
font-size:$normal-plus;
}//end laptop

& > strong{
color: $color-services-text;
font-weight:550;
}//end strong
}//end p
}//end section

//postion 1
& > .web-starter{
grid-column: 1;
grid-row:1;
}//end development

//position 2
& > .design{
grid-column: 1;
grid-row:2;

@include responsive(phone){
grid-column: 2;
grid-row: 1;
}
}//end web-starter

//position 3
& > .development{
grid-column: 1;
grid-row:3;

@include responsive(phone){
grid-column: 1;
grid-row: 2;
}

@include responsive(big-tablet){
grid-column: 3;
grid-row: 1;
}
}//end troubleshoot

//position 4
& > .content{
grid-column: 1;
grid-row:4;

@include responsive(phone){
grid-column: 2;
grid-row: 2;
}

@include responsive(big-tablet){
grid-column: 1;
grid-row: 2;
}

@include responsive(desktop){
grid-column:4;
grid-row: 1;
}
}//end design

//position 5
& > .refactoring{
grid-column: 1 ;
grid-row:5;

@include responsive(phone){
grid-column: 1;
grid-row: 3;
}

@include responsive(big-tablet){
grid-column: 2;
grid-row: 2;
}
@include responsive(developer){
grid-column:5;
grid-row: 1;
}
}//end content

//position 6
& > .troubleshoot{
grid-column: 1;
grid-row:6;

@include responsive(phone){
grid-column: 2;
grid-row: 3;
}

@include responsive(big-tablet){
grid-column: 3;
grid-row: 2;
}

@include responsive(developer){
grid-column:6;
grid-row: 1;
}
}//end refactor
}//end s-row

& > .contact{
grid-column: 1 / -1;
grid-row:3;
//margin-bottom:1rem;
display:flex;
justify-content:center;
align-items:center;

& > a{
height:50px;
display:flex;
justify-content:center;
align-items:center;
}
}//end contact

}//end services
