//psuedo class for background image, enables shifting
.header:before {
transition: transform 4s  cubic-bezier(0,1,.3,1);
will-change: transform;
background-size: cover;
content: "";
position: absolute;
top: 0;
height:93vh;
left: 0;
right:0;
overflow:hidden;

//works with header margin offset, switch direction at desktop
transform: translatex(7rem);
//offset changes when direction changes (protag moves forward)
@include responsive(desktop){
transform: translatex(-7rem);
}//end desktop
}//end header:before
.header.is-visible:before {
transform:none;
opacity: 1;
}//end header is visible

//header section
.header {
grid-column: 1 / -1;
grid-row: 1 / 3;
display:grid;
grid-template-columns:1fr;
grid-template-rows:1fr .8fr .8fr;
height:93vh;
overflow:hidden;
position:relative;
transform-style: preserve-3d;
perspective: 100px;
//works in conjuction with header:before transform: translatex(7rem)
margin-left:-7rem;
// header media queries
@include responsive(small-phone){
}
@include responsive(phone){
}
//768
@include responsive(tablet){
grid-row:1;
grid-template-columns: 1fr .4fr;
grid-template-rows:1fr .8fr .8fr;
}
@include responsive(big-tablet){
grid-template-columns:1fr 1fr;
}
@include responsive(desktop){
//works in conjuction with desktop(){header:before transform: translatex(-7rem)
//remove previous margin offset, then add a new one
margin-left:0rem;
margin-right:-7rem;
grid-template-columns:.8fr 1fr;
}//end desktop
@include responsive(big-desktop){
grid-template-columns:1fr 1fr;
//grid-template-columns:1fr 1fr;
}

//logo box section
& > .logo-box {
@include background(none, $color-primary,1, contain);
grid-column:1 / 2;
//logo spans also navigation to enable positing
grid-row:1 / 3;
