.samples {
grid-column: 1 / -1;
grid-row:6;
display:grid;
grid-template-columns: 1f;
grid-template-rows: 70px auto;
background:$color-primary;
width:100%;
height:100%;

@include text-template($small,$font-family-ads, $color-primary);
min-height:40vh;

//team sub-grid media queries
@include responsive(phone){
grid-template-columns: 1fr ;
}

@include responsive(tablet){
grid-template-columns: 1fr 1fr 1fr;
//min-height:30vh;
}

& > .samples_header{
grid-column: 1 / -1;
grid-row:1;
//width:110%;
display:flex;
align-items:center;
justify-content:center;
background:$color-dark-headers;
text-align:center;

& > h2{
color:$color-primary;
}
}//end header

& > section{
display:flex;
width:100%;
align-items:center;
justify-content:center;

&  > a{
height:80px;
color:$color-samples-title;
text-decoration:none;
display:flex;
flex-wrap:wrap;
align-items:center;

//text size samples links
@include responsive(small-phone){
font-size: $massive;
}

@include responsive(galaxy-fold){
font-size: $jumbo;
}
//pivot reverse order
@include responsive(phone){
font-size: $massive;
}
@include responsive(tablet){
font-size:$large;
}
@include responsive(big-tablet){
font-size:$jumbo;
}
@include responsive(laptop){
}
@include responsive(desktop){
font-size:$massive;
}
@include responsive(big-desktop){
font-size:$xlarge;
}
}//end a

& > a:hover, & > a:hover:focus {
color:$color-samples-title-hover;
transform: scale(1.2);
}
}//end section

& > .samples-1{
grid-column:1;
grid-row:2;
justify-self:center;
background-color:transparent;
@include responsive(phone){
grid-column: 1;
}
@include responsive(tablet){
grid-column: 1;
}//end responsiveP

& > a{
font-family: $font-family-cloud-nine;
font-weight:800;
}
}//end person 1

& > .samples-2{
position:relative;
grid-column:1;
grid-row:3;

@include responsive(tablet){
grid-column:2;
grid-row:2;
}

@include responsive(big-tablet){
justify-content:start;
}

& > a{
font-family: $font-family-software;
font-weight:800;
@include responsive(small-phone){
font-size: $supersized;
}
@include responsive(phone){
font-size: $supersized;
}
@include responsive(tablet){
font-size:$massive;
}
@include responsive(big-tablet){
font-size:$xlarge;
}
@include responsive(laptop){
}
@include responsive(desktop){
font-size:$supersized;
}
@include responsive(big-desktop){
font-size:$gargantuan;
}
}
}//end person 2

& > .samples-3{
grid-column:1;
grid-row:4;
justify-self:center;
@include responsive(phone){
}
@include responsive(tablet){
grid-column:3;
grid-row:2;
}

& > a{
font-weight:800;
}
}//end person 3
}//samples

.mock-up{
grid-column: 1 / -1;
grid-row:  6 /  8;
align-self:center;
display:none;
z-index:8;
padding:0px;
margin:-850px 0px 0px 0px;
width:100%;

@include responsive(phone){
}//

@include responsive(phone){
display:grid;
margin:50px 0px 0px 0px;
grid-column: 6 / 13;
}//
@include responsive(tablet){
grid-column:  6 / 13;
}//
@include responsive(big-tablet){
grid-column: col-start 7 / 13;

}//
@include responsive(laptop){
grid-column: col-start 7 / 13;
}//
@include responsive(big-desktop){
}//

& > .mock-up-image{
width:90%;
height:auto;
justify-self:center;
align-self:end;
margin:0px 0px 0px 0px;
object-fit:contain;
object-fit:cover;
box-shadow: 0 48px 48px 5px rgba(0,0,0,1);
transition: transform 1s cubic-bezier(0.68, -0.6, 0.32, 1.6);
transform: scale(.2);
opacity:0;
will-change: transform, opacity;

@include responsive(big-tablet){
}//
@include responsive(laptop){
}//
@include responsive(big-desktop){
}//

//visible and flips mock-up image
&.is-visible{
opacity:1;
transform: scale(1) rotateZ(360deg);
}
}// end mock-up-image
}//end mock-up

//click text, sample-mock-up, gets visible
.mock-up.sample-visible{
display:grid;
}//sample visible

//click visible sample-mock-up, image disappears
.mock-up.sample-visible:hover{
cursor:pointer;
}
