.banner:before{
background-size:cover;
content: "";
position: relative;
//top: 0;
height:100vh;
grid-column: 1 / -1;
grid-row:1/3;
z-index:1;
//left: 0;
//right:0;
@include responsive(iphone-5){
height:120vh;
}

@include responsive(galaxy-fold){
height:115vh;
}
}//end banner:before

.banner {
grid-column:1 / -1;
grid-row:4;
height:100vh;
display:grid;
grid-template-columns: 1fr;
grid-template-rows: 70px auto;
position: relative;
overflow:hidden;
z-index:1;
//text-r adjust size responsively
@include text-r($font-family-ads, $normal, 400, $color-primary);
background:transparent;

@include responsive(small-phone){
}

@include responsive(pixel-2){
}

@include responsive(iphone-x){
}

@include responsive(iphone-5){
height:120vh;
}

@include responsive(galaxy-fold){
height:115vh;
}

& > .b_header{
grid-column: 1 / -1;
grid-row: 1;
height: 100%;
display: flex;
align-items: center;
justify-content: center;
text-align: center;
background:$color-dark-headers;
z-index:2;

& > h2{
color:$color-primary;
}//end h2


}//header

@mixin typing-animation {
animation: type 4s steps(60, end), appear .1s ease-in-out;
}

@mixin animateh3 {
@include text-r($font-family-courier,$massive,400,$color-primary);
position: relative;
text-align: center;
margin: .5rem 0;
white-space: nowrap;
overflow: hidden;
width: 100%;

@include responsive(small-phone){
font-size:$less-big;
//margin: .1em 0;
}

@include responsive(iphone-5){
font-size:$normal;
}
@include responsive(galaxy-fold){
font-size:$small;
}
//forward cascade
@include responsive(phone){
font-size: $big;
}

@include responsive(tablet){
font-size:$med;
margin: .5rem 0;
}
@include responsive(big-tablet){
font-size:$large;
}
@include responsive(laptop){
font-size:$larger;
width: 90%;
}
@include responsive(desktop){
font-size:$jumbo;
width: 85%;
}
@include responsive(big-desktop){
font-size:$massive;
width: 80%;
}
@include responsive(developer){
font-size:$xlarge;
}
@include responsive(tv){
font-size:$gargantuan;
}
}//end animate h3 mixin

//put hello inside banner at big-tablet size, change things from there

