.contact{
grid-column: 1 / -1;
grid-row:9;
display:grid;
grid-template-columns: 1fr 20f 1fr;
grid-template-rows: 70px auto;
justify-items:center;
align-items:center;

background:$color-primary;
width:100%;
height:100%;


@include text-template($small,$font-family-ads, $color-primary);
//grid-gap:2rem;

//contact sub-grid media queries
@include responsive(phone){
grid-template-columns: 1fr 15f 1fr;
}
@include responsive(tablet){
grid-template-columns: 1fr 8fr 1fr;
}
@include responsive(laptop){
grid-template-columns: 1fr 4fr 1fr;
}

@include responsive(big-desktop){
grid-template-columns: 1fr 2fr 1fr;
}

& > .contact_header{
grid-column:1 / 4;
grid-row:1;
display:flex;
align-items:center;
justify-content:center;
background:$color-dark-headers;
@include text-template($small,$font-family-ads, $color-primary);
text-align:center;
width:100%;
height:100%;

& > h2{
color:$color-primary;
}//
}//end header

& > .contact_form{
grid-column:2;
grid-row:2;

& > p{
margin:15px 0px 0px 0px;
@include text-template($big,$font-family-ads, $color-primary);
color:$color-contact-instructions;
text-align:center;
width:100%;
}//end p

& .form-text{
color:$color-contact-input;
margin: 0px 0px 0px 15px;
}//

}//end contact__form
}// end contact
