.mission{
grid-column: 1 / -1;
grid-row:3;
display:grid;
width:100%;
grid-template-columns:1fr;
grid-template-rows:auto;
background:$color-primary;

@include responsive(tablet){
grid-template-columns:1.7fr 1fr;
}

@include responsive(laptop){
grid-template-columns:1fr 1fr;
}

// navigation in mission grid no grid
& > &__navigation{
grid-column:1;
grid-row:1;
background:$color-primary;
margin:32px 32px 16px 32px ;
height:auto;
border-radius:$header-b-rad;
padding:16px;

@include responsive(tablet){
margin:32px 16px 32px 32px ;
}//end tablet

& > .nav-tabs > li > a{
border-bottom: 1px solid $color-mission-list-b-border;
letter-spacing: 2px;
font-size: 13px;
text-transform: uppercase;
font-weight: 700;
color: $color-mission-list-link;
}

& > .tab-content{
transition: 0.5s;
margin-block-start: 1em;
margin-block-end: 1em;
margin-inline-start: 0px;
margin-inline-end: 0px;
font-family:$font-family-base;
font-weight: 300;
color:$color-mission-tab-content;


& > .tab-pane{
min-height:600px;
margin-bottom:30px;


@include responsive(phone){
min-height:400px;
}

@include responsive(tablet){
min-height:600px;
}

& > h3{
line-height: 1.8;
margin-bottom:30px;

@include responsive(tablet){
font-size: $less-big;
line-height:1.6;
}

@include responsive(desktop){
font-size: $big;
line-height: 1.8;
}

} //end h3

& > p{
font-size: $normal-plus;
margin-bottom:30px;

@include responsive(tablet){
font-size: $normal;
line-height: 1.5;
}

@include responsive(desktop){
font-size: $normal-plus;
line-height: 1.8;
}

}

& > ul{
margin-left:40px;
width:100%;


& > li{
font-size: 18px;
line-height: inherit;

& > .icon-mission{
@include icons;
}//end mission
}//end li
}// ul

}//end tab pane

//the button
& > p{
display: flex;
justify-content: center;
align-items: center;

& > a{
height: 50px;
display: flex;
justify-content: center;
align-items: center;
}//end a
}//end p
}//end tabcontent

}//end navigation

//in mission grid has grid
& > &__brands{
grid-column:1;
grid-row:2;
display:grid;
grid-template-columns:1fr;
grid-template-rows:auto;
grid-gap:2rem;
margin:32px;


@include responsive(phone){
grid-template-columns:1fr 1fr;
}//end phone
@include responsive(tablet){
grid-column:2;
grid-row: 1;
grid-template-columns:1fr;
}
// changing heights avoids long narrow rectangles
@include responsive(laptop){
grid-template-columns:1fr 1fr;
height:60%;
}
@include responsive(desktop){
height:80%;
}
@include responsive(big-desktop){
height:auto;
}

//center align images
& > picture{
display:grid;
justify-content:center;
align-content:center;
background:$color-primary;
border-radius:$header-b-rad;
min-height:400px;

// images should be identical sizes, did this instead, oops
@include responsive(small-phone){
min-height:200px;
}//end phone

@include responsive(galaxy-fold){
min-height:150px;
}//end phone


@include responsive(phone){
min-height:250px;
}//end phone
@include responsive(tablet){
min-height:225px;
}
@include responsive(laptop){
min-height:200px;
}
}//end picture

& > .wp{
grid-column:1;
grid-row:1;
}//wp

& > .avada{
grid-column:1;
grid-row:2;

@include responsive(phone){
grid-column: 2;
grid-row: 1;
}
@include responsive(tablet){
grid-column: 1;
grid-row: 2;
}
@include responsive(laptop){
grid-column: 2;
grid-row: 1;
}
}//avada

& > .woocom{
grid-column:1;
grid-row:3;

@include responsive(phone){
grid-row: 2;
}
@include responsive(tablet){
grid-row: 3;
}
@include responsive(laptop){
grid-row: 2;
}
}//woocom

& > .hugo{
grid-column:1;
grid-row:4;

@include responsive(phone){
grid-column:2;
grid-row:2;
}
@include responsive(tablet){
grid-column:1;
grid-row:4;
}
@include responsive(laptop){
grid-column:2;
grid-row:2;
}
}//end hugo

}//end brands

}//end mission
