.blog {
grid-column: 1 / -1;
grid-row:8;
display:grid;
grid-template-columns: 1f;
grid-template-rows: 70px auto;
min-height:30vh;

background:$color-primary;
width:100%;
height:100%;


@include text-template($small,$font-family-ads, $color-primary);
//grid-gap:2rem;
//background:$color-dark;

//team sub-grid media queries
@include responsive(phone){
grid-template-columns: 1fr ;
}

@include responsive(tablet){
grid-template-columns: 1fr ;
min-height:30vh;
}

& > .blog_header{
grid-column:1 / 2;
grid-row:1;
display:flex;
align-items:center;
justify-content:center;
height:100%;
//margin:0px 0px 0px 0px;
//min-width:110%;
background:$color-dark-headers;
text-align:center;
@include responsive(phone){
//grid-column: 1 / span 2 ;
}
@include responsive(laptop){
//grid-column: 1 / span 4 ;
}

& > h2{
color:$color-primary;
}

}//end blog header

& > .recent-container{
grid-column:1 / 2;
grid-row:2;
padding:0px 15px 40px 15px;
display:flex;
flex-wrap:wrap;
justify-content:space-around;

& > .img-end{
width:100%;
display:flex;
justify-content:space-between;
text-align:center;
flex-wrap:wrap;

& > p{
}

& > .previews{
width:48%;
padding:$pad-zero;
margin:40px 0px 0px 0px;
height:150px;
display:flex;
text-align:center;
flex-wrap:wrap;

@include responsive(phone){
width:45%;
height:200px;
}
@include responsive(tablet){
width:30%;
height:210px;
}
@include responsive(desktop){
width:30%;
height:170px;

}

& > a {
width:100%;
font-size:$small;
height:100%;
padding:$pad-zero;
color: $color-dark;
text-decoration:none;
border-radius: $rad-normal;
@include background(none,$color-primary, .9, cover);

@include responsive(phone){
font-size:$normal;
}

@include responsive(tablet){
font-size:$small;
}

@include responsive(desktop){
font-size:$small;
}

& > div{
background-size: cover;
background-repeat: no-repeat;
background-position: center;
border-radius:4px 4px 0px 0px;
width:100%;
height:90px;
display:flex;
justify-content:flex-end;
align-items:flex-end;

//margin:0px auto 0px auto;
padding:10px 10px 10px 10px;
@include responsive(phone){
height:115px;
}
@include responsive(tablet){
height:115px;
}
@include responsive(big-tablet){
height:130px;
}
@include responsive(laptop){
height:130px;
}
@include responsive(desktop){
height:100px;
}
@include responsive(big-desktop){
height:100px;
}

& > p{
border-radius:$rad-normal;
width:max-content;
background: $color-blog-previews-background; 
//padding:10px 10px 10px 10px;
padding:5px;
text-align:left;
@include text-template($small,$font-family-ads, $color-feature-text);
}

}//end div with backgrouund image

&:hover {
@include background(none,$color-primary, 1, cover);
transform: scale(1.1);
font-size:101%;
color:$color-blog-previews-hover;
box-shadow: 0 48px 48px 5px rgba(0,0,0,1);
}

&:visited {
@include background(none,$color-primary, 1, cover);
color:$color-blog-previews-hover;
}
}//end a

}//end previews

}//img-end
} // end recent container

}//end blog
