.team{
grid-column:  1 / -1;
grid-row:7;
display:grid;
grid-template-columns:1fr;
//sets height of black section header
grid-template-rows:70px minmax(600px, 1fr);

background:$color-primary;
width:100%;
height:100%;

@include text($Open-Sans,$normal,700, $color-team-text);

//team sub-grid media queries
@include responsive(phone){
grid-template-columns: 1fr 1fr ;
}

@include responsive(tablet){
grid-template-columns: 1fr 1fr;
}

@include responsive(big-tablet){
grid-template-columns: 1fr 1fr 1fr 1fr;
}

& > &_header{
grid-column:1 / -1;
grid-row:1;
display:flex;
align-items:center;
justify-content:center;
background:$color-dark-headers;
text-align:center;
@include text-template($small,$font-family-ads, $color-primary);

& > h2{
color:$color-primary;
}
}//end header

//gradient (unused) mixin needs color variable
//$color1: get-color('ceramic', 1);
//$color2: get-color('blackest', 1);

//person sections
& > section:nth-child(n + 2){
display:grid;
grid-template-columns:1fr;
//grid-template-rows:1fr 0.1fr;
grid-template-rows:0.7fr 1fr 0.1fr;
position:relative;
border-radius: $header-b-rad;
padding: 1.2rem;
margin:2rem;
//color:$color-dark;
background:$color-primary;
text-align:center;

@include responsive(small-phone){
grid-template-rows:1fr 1fr 0.1fr;
}
@include responsive(pixel-2){
grid-template-rows:.7fr 1fr 0.1fr;
}
@include responsive(iphone-x){
grid-template-rows:.6fr 1fr 0.1fr;
}
@include responsive(iphone-5){
}
@include responsive(galaxy-fold){
grid-template-rows:.8fr 1fr 0.1fr;
}
//forward cascade
@include responsive(phone){
grid-template-rows:.6fr 1fr 0.1fr;
}
@include responsive(tablet){
grid-template-rows:.8fr 1fr 0.1fr;
}
@include responsive(big-tablet){
}
@include responsive(laptop){
grid-template-rows:.7fr 1fr 0.1fr;
}
@include responsive(desktop){
grid-template-rows:1fr 1fr 0.1fr;
}
@include responsive(big-desktop){
grid-template-rows:1fr 1fr 0.1fr;
}
@include responsive(developer){
grid-template-rows:1fr 1fr 0.1fr;
}
@include responsive(tv){
}

//appearing stats panel
& > .stats{
grid-column:1;
grid-row:1 / 4;
display: grid;
grid-template-columns:.5fr 1fr;
grid-template-rows:.4fr auto;
//background-image: linear-gradient(-245deg, rgba($color1, 1),2%, rgba($color2, 1)), url(none);
background: $color-stats-background;

box-shadow: 0 48px 48px 5px rgba(0,0,0,1);
border-radius: $header-b-rad;
color: #fff;
opacity: 0;
padding: $pad-normal;
transform-origin: 0 0;
transform: scale(.3, .3);
width:100%;
will-change:transform, opacity;
transition: transform 1s ease-out, opacity .5s ease-out;
height:90%;

//come back to this
@include responsive(phone){
height:100%;
grid-template-rows:.5fr auto;
}//end phone

@include responsive(big-tablet){
height:100%;
grid-template-rows:.8fr auto;
}//end phone

@include responsive(laptop){
grid-template-rows:.9fr auto;
}//end phone

@include responsive(desktop){
grid-template-rows:.6fr auto;
//grid-template-rows:1fr auto;
//height:90%;
}//end phone

@include responsive(big-desktop){
grid-template-rows:.55fr auto;
}//end phone

@include responsive(developer){
grid-template-rows:.5fr auto;
}//end phone

@include responsive(tv){
grid-template-rows:.3fr auto;
}//end phone

//team-member stats div
& > div {
grid-column:2;
grid-row:1;
width:100%;
height:100%;
align-self:start;

> h3 {
margin:0px auto 5px auto;
text-align:center;
background: $color-stats-title; 
border-radius:$rad-normal;
width:80%;

@include responsive(phone){
font-size:$big;
}//end phone

@include responsive(tablet){
font-size:$med;
}//end phone

}//end h3

& > h4 {
width:100%;
color:$color-primary;
}
}/*end team-member stats div*/

& > .table{
grid-column:1/3;
grid-row:2;
align-self:start;
margin:0px 0px 0px 0px;
border:none;
//without this height is massive on mozilla
height:0%;
border-collapse: collapse;
display:flex;
@include text-template($small,$font-family-ads, $color-primary);

@include responsive(phone){
//margin:0px 0px 0px 0px;
}//end phone

@include responsive(big-tablet){
//margin:5px 0px 0px 0px;
}//end phone

@include responsive(desktop){
//stops table overlap image
//margin:70px 0px 0px 0px;
//margin-top:70px;
}//end phone

@include responsive(big-desktop){
//margin-top:0px;
//margin:5px 0px 0px 0px;
}//end phone

& > tbody{
width:100%;
//gets the table to work
display:table;
table-layout:fixed;

& tr{
& > .table-head{
text-align:center;
padding: 10px 0px;

& > h4{

@include responsive(phone){
font-size:$less-big;
}//end phone

@include responsive(big-tablet){
font-size:$big;
}//end phone

@include responsive(desktop){
//font-size:$less-big;
}//end phone

@include responsive(big-desktop){
//font-size:$big;
}//end phone

}//end h4

}//end tableheader

//styles 
& > .table-head:after {
content: ""; /* This is necessary for the pseudo element to work. */
display: block; /* This will put the pseudo element on its own line. */
margin: 0 auto;  /* This will center the border. */
width: 60%;
padding-top: 10px 0px 0px 0px;
border-bottom:1px solid #fff;
}

&  td, &  th {
border: none;
padding: 0px;
vertical-align: center;
//keeps right table text centered (because left styled in li)
text-align:left;
@include text($font-family-mono,$small,400, $color-primary);

@include responsive(phone){
font-size:$tiny;
}//end phone

@include responsive(big-tablet){
font-size:$small;
}//end phone
}

&  td{

&  li{
@include text($font-family-mono,$small,400, $color-primary);
//keeps left table text centered
text-align:right;
list-style-type: none;

@include responsive(phone){
font-size:$tiny;
}//end phone

@include responsive(big-tablet){
font-size:$small;
}//end phone
}
}//end table data
}//table row
}//end table-head

}//table

//text appears
& > h3:hover h3, & > li:hover {
opacity: 1;
}
& > h3 {
//margin: 0 0 1em;
text-align: right;
}

&.is-hovered{
opacity:1;
box-shadow: 0 48px 48px 5px rgba(0,0,0,1);
transform: none;
}
} //end stats

& > .image-holder{
grid-column:1;
grid-row:1;
display:grid;
grid-template-columns: 1fr;
grid-template-rows: 1fr;
height:100%;
width:100%;
position:relative;

& > img{
grid-column:1;
grid-row:1;
top:0%;
left:0%;
transform: scale(1.5);
margin-right:0;
will-change:transform;
z-index:5;
border-radius:$rad-big;
background: $color-team-image-background; 
height:4em;
width:4em;

@include responsive(small-phone){
margin-top: calc(50% - 10em);
margin-left: calc(50% - 3em);
height:8em;
width:8em;
}
@include responsive(pixel-2){
margin-top: calc(50% - 8em);
margin-left: calc(50% - 3em);
height:6em;
width:6em;
}
@include responsive(iphone-x){
margin-top: calc(50% - 6em);
}
@include responsive(iphone-5){
margin-top: calc(50% - 5em);
}
@include responsive(galaxy-fold){
margin-top: calc(50% - 2em);
margin-left: calc(50% - 2em);
height:4em;
width:4em;
transform: scale(1.8);

}
@include responsive(phone){
margin-top: calc(50% - 6em);
margin-left: calc(50% - 3em);
height:6em;
width:6em;
}
@include responsive(tablet){
margin-top: calc(50% - 6em);
margin-left: calc(50% - 3em);
height:6em;
width:6em;
}
@include responsive(big-tablet){
margin-top: calc(50% - 5em);
margin-left: calc(50% - 3em);
height:6em;
width:6em;
}
@include responsive(laptop){
margin-top: calc(50% - 7em);
margin-left: calc(50% - 3em);
height:6em;
width:6em;
}
@include responsive(desktop){
margin-top: calc(50% - 8em);
margin-left: calc(50% - 3em);
height:6em;
width:6em;
}
@include responsive(big-desktop){
margin-top: calc(50% - 8em);
margin-left: calc(50% - 3em);
height:6em;
width:6em;
}
@include responsive(developer){
margin-top: calc(50% - 10em);
margin-left: calc(50% - 4em);
height:8em;
width:8em;
}
}//end image
}                               // image-holder

& > .image-holder {

& > .img-hover{

&.is-hovered{
cursor:pointer;
//background:transparent;
//transform: scale(.6);
@include profile-center;
//transform:none;
//justify-self:none;
//margin-top: .7em;
//margin-left: .7em;
}//is-hovered
}//img-hover
}//image-holder is hovered

& > .stats:hover{
opacity:1;
box-shadow: 0 48px 48px 5px rgba(0,0,0,1);
transform:none;
}

& > .bio{
grid-column:1;
grid-row:2;
//align-self:center;
height:100%;
width:100%;
overflow:hidden;
//margin-bottom:5em;

& > h4{
@include text-r($font-family-nav, $big,700, $color-team-bio-headers);
margin:0px 0px 10px 0px;
}

& > h6{
@include text-r($font-family-nav, $big,700, $color-team-bio-headers);
margin:0px 0px 15px 0px;
}

& > p{
@include text-r($font-family-ads, $normal, 500, $color-team-bio-text);
line-height:26px;

@include responsive(small-phone){
font-size:$normal;
}

@include responsive(phone){
font-size:$small;
}
@include responsive(tablet){
font-size:$normal;
}
@include responsive(tablet){
font-size:$normal;
}
}//end paragraph
}//end bio

& > ul{
grid-column:1;
grid-row:3;
display:flex;
flex-direction:row;
justify-content:center;
margin:15px 0px 0px 0px;

//.icon-team
& > li{
border:none;
font-size: $normal-plus;
padding: .75rem 1.25rem;

@include responsive(small-phone){
padding: .75rem .9rem;
font-size: $small;
}

@include responsive(galaxy-fold){
padding: .75rem .6rem;
font-size: $small;
}

@include responsive(phone){
padding: .75rem .9rem;
font-size: $normal;
}

@include responsive(tablet){
font-size: $normal;
padding: .75rem 1rem;
}

@include responsive(desktop){
font-size: $normal-plus;
padding: .75rem 1.25rem;
}

//.icon-team > i
& > a > i{
color:$color-team-icons;
}
}//icon eam
}//ul

} //end people sections

& > section.person-1{
grid-column:1 / 3;
grid-row:2;

&  h4{
color: $color-primary;
}

@include responsive(phone){
grid-column: 1;
}
@include responsive(big-tablet){
width:60%;
grid-column: 1 / span 2;
justify-self:center;
}

}//end person 1

& > section.person-2{
grid-column:1;
grid-row:3;
background:blue;
display:none;
@include responsive(phone){
grid-column:2;
grid-row:2;
}
@include responsive(laptop){
grid-column: 2;
}
}//end person 2

& > section.person-3{
grid-column:1;
grid-row:4;
background:orange;
display:none;
@include responsive(phone){
grid-column:1 ;
grid-row:3;
}
@include responsive(laptop){
grid-column:3;
grid-row:2;
}
}//end person 3

& > section.person-4{
grid-column:1;
grid-row:5;
background:purple;
display:none;

@include responsive(phone){
grid-column:2;
grid-row:3;
}
@include responsive(laptop){
grid-column:4 ;
grid-row:2;
}
}//end person 4

}//team
